
#app {
  font-family: "Iropke Batang","IropkeBatang","Apple SD Gothic Neo","Malgun Gothic",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000000;
  max-width: 820px;
  margin: 0 auto;
  text-align: left;
}
.header {
  margin: 1em;
}
.title {
  display: inline-block;
  margin: 0 0.8em 0 0;
}
.title a {
  color:#000000;
  text-decoration: none;
}
.header-nav {
  display: inline-block;
}
.header-nav ul {
  list-style: none;
  padding:0;
}
@media screen and (max-width: 450px) {
.header-nav {
    display: block;
}
.title {
    display: block;
    margin: 0;
}
.header {
    text-align: center;
}
}
.header-nav ul li {
  display: inline-block;
  margin: 0 0.3em;
  padding: 0 0.5em;
}
.header-nav ul li a {
  color:#000000;
  display: block;
  text-decoration: none;
}
.underline {
  border-width: 0 0 2px;
  border-style: solid;
}

.markdown-wrap {
  padding: 1em;
}
.markdown-wrap ul {
  list-style-position: inside;
  padding: 0;
}
.markdown-wrap a {
  text-decoration: none;
}
